import { Link } from "@remix-run/react";
import { useState } from "react";

export default function AdminLayout({ children }) {
    const [open, setOpen] = useState(true);

    return (
        <div className="min-h-screen flex bg-gray-100">
            <aside
                className={`bg-gray-800 absolute top-0 text-white w-64 transition-all duration-300 ${open ? "" : "w-0"
                    }`}
            >
                <button
                    onClick={() => setOpen(!open)}
                    className="p-4 text-white focus:outline-none"
                >
                    {open ? "←" : "→"}
                </button>

                <nav className="p-2 space-y-2">
                    <Link to="/admin" className="block px-3 py-2 hover:bg-gray-700">
                        
                    </Link>
                    <Link to="/admin/kitchen" className="block px-3 py-2 hover:bg-gray-700">
                        Kitchen
                    </Link>
                </nav>
            </aside>

            <main className="flex-1 p-6 overflow-y-auto">{children}</main>
        </div>
    );
}
